@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  font-family : "Jost",sans-serif;
}

body{
	width: 100%;
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	-webkit-align-items: center;
	        align-items: center;
}

.box {
	width: 40vw;
	height: 80vh;
	background: rgb(103, 212, 231);
	box-shadow: 5px 5px 15px -5px rgb(0, 0, 0);
	border-radius: 15px;
	position: relative;
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
	        transform: translate3d(0, 0, 0);
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	-webkit-align-items: center;
	        align-items: center;
}

  

.box::after{
  content:" ";
  display:block;
  left:0;
  top:0;
  width:100%;
  height: 100%;
  z-index:11;
  -webkit-transform:translate3d(0,0,0);
          transform:translate3d(0,0,0)
}

#weathercon{
  margin-top: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content:center;
          justify-content:center;
  -webkit-align-items:center;
          align-items:center;
  height:40%;
  font-size: 3rem;
}

#weathercon .fas{
  font-size: 6rem;
  -webkit-animation: fas-anime 2s infinite alternate;
          animation: fas-anime 2s infinite alternate;
}

@-webkit-keyframes fas-anime{
  from{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  to{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  } 
}

@keyframes fas-anime{
  from{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  to{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  } 
}


.input {
  position:relative;
  width: 60%;
  top:10px;
	border-radius: 10px;
}


.wave{
  opacity: 0.3;
  position: absolute;
  top: 90%;
  left:50%;
  background: white;
  width:40rem;
  height: 40rem;
  margin-left: -20rem;
  margin-top: -15rem;
  -webkit-transform-origin: 50% 48%;
          transform-origin: 50% 48%;
  border-radius: 40%;
  -webkit-animation: drtift 1000ms infinite linear;
          animation: drtift 1000ms infinite linear;
  z-index:1;
}

.wave.-three{
  -webkit-animation: drift 5000ms infinite linear;
          animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity:0.2;
}

.wave.-two{
  -webkit-animation: drift 7000ms infinite linear;
          animation: drift 7000ms infinite linear;
  z-index:3 ;
  opacity:0.1;
}

@-webkit-keyframes drift {
  from{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  
}

@keyframes drift {
  from{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  
}


.fa-street-view{
  -webkit-animation:rotates 3s linear infinite alternate;
          animation:rotates 3s linear infinite alternate;
}

@-webkit-keyframes rotates{
  from{
    -webkit-transform: translateX(-0.5rem);
            transform: translateX(-0.5rem);
  }
  to{
    -webkit-transform: translateX(0.5rem);
            transform: translateX(0.5rem);
  }
}

@keyframes rotates{
  from{
    -webkit-transform: translateX(-0.5rem);
            transform: translateX(-0.5rem);
  }
  to{
    -webkit-transform: translateX(0.5rem);
            transform: translateX(0.5rem);
  }
}


.info{
  width: 100%;
  height: 45%;
  position: absolute;
  bottom:0;
  z-index:4;
}

.err{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  width:100%;
  height: 100%;
  font-weight: bold;
}

@media (max-width:850px){
  .box{
    width: 90vw;
  }

  .wave{
    height: 70rem;
    width: 70rem;    
    margin-left: -30rem;
    margin-top: -10rem;
  }
}
